import React, { useState } from "react";
import { Link } from "react-router-dom";
import { Navbar, Nav, Dropdown } from "react-bootstrap";
import "./Header.css";

export default function Header() {
  const [isOpen, setOpen] = useState(false);
  return (
    <Navbar className="header-bar">
      <Link to={"/#home"} className={"header-item"}>
        HOME
      </Link>
      <Link to={"/#about"} className={"header-item"}>
        ABOUT
      </Link>
      <Link to={"/#resume"} className={"header-item"}>
        RESUME
      </Link>
      <Dropdown
        onMouseEnter={() => {
          setOpen(true);
          document.querySelector(".header-bar").style = "background: #222222";
        }}
        onMouseLeave={() => {
          setOpen(false);
          if (window.scrollY < 73)
            document.querySelector(".header-bar").style =
              "background: transparent";
        }}
      >
        <Link to={"/#projects"} className={"header-item"}>
          PROJECTS
        </Link>
        <div
          className="project-dropdown"
          style={{
            height: isOpen ? "240px" : "0",
            // opacity: isOpen ? "1" : "0",
            background: isOpen ? "#222222" : "transparent",
          }}
        >
          {[
            "Covid 19 Tracker",
            "E-Commerce Website",
            "Chat Application",
            "Menu Hosting Service",
            "iPark IOT Parking System",
            "Robot Arm",
            "Self-Balancing Robot",
            "XFOX Image Plotter",
          ].map((d, i) => (
            <Dropdown.Item
              key={`projects-${i}`}
              as={Link}
              to={`/#projects-${i}`}
              className={"header-item"}
            >
              {d}
            </Dropdown.Item>
          ))}
        </div>
      </Dropdown>

      <Link to={"/#contact"} className={"header-item"}>
        CONTACT
      </Link>
    </Navbar>
  );
}
