import React from "react";
import { Button } from "react-bootstrap";
import image from "../../assets/img/profile.png";
import "./About.css";

export default function About() {
  return (
    <div id={"#about"} className="about-container">
      <div className="image">
        <img
          style={{ width: "100%", borderRadius: "100%" }}
          src={image}
          alt="Profile"
        />
      </div>
      <div className="text">
        <h2>ABOUT ME</h2>
        <p>
          I am an engineer graduated from SFU with a Bachelor Degree in
          Mechatronic Systems Engineering. My undergraduate studies have exposed
          me to extensive experience in Robotic System and Control System. With
          my co-op experience, I have developed my interest in software
          industry. During my leisure time, I like to learn new modern software
          technologies and sharpen my skills, and apply them to create some web
          projects. I am pursuing a career in Mechatronics Engineering /
          Software Development. For more information, please view my resume and
          check out my projects below.
        </p>
        <h2 style={{ paddingBottom: "10px" }}>RESUME</h2>
        <Button variant="outline-light" href="/Resume.pdf" download>
          <i class="far fa-file-pdf"></i> Download PDF
        </Button>
        <p>or...</p>
        <Button variant="outline-light" href="/#resume">
          <i class="fas fa-link"></i> Nav Me!
        </Button>
      </div>
    </div>
  );
}
