import React, { Component } from "react";
import Particles from "react-tsparticles";
import "./Background.css";

class Backgound extends Component {
  render() {
    return (
      <Particles
        id="tsparticles"
        className="canvas-bg"
        options={{
          fpsLimit: 60,
          interactivity: {
            detectsOn: "canvas",
            events: {
              onClick: {
                enable: true,
                mode: "push",
              },
              onHover: {
                enable: true,
                mode: "repulse",
              },
              resize: true,
            },
            modes: {
              bubble: {
                distance: 400,
                duration: 2,
                opacity: 0.4,
                size: 40,
              },
              push: {
                quantity: 4,
              },
              repulse: {
                distance: 200,
                duration: 0.4,
              },
            },
          },
          particles: {
            color: {
              value: "#ffffff",
            },
            links: {
              color: "#ffffff",
              distance: 150,
              enable: true,
              opacity: 0.1,
              width: 1,
            },
            collisions: {
              enable: true,
            },
            move: {
              direction: "none",
              enable: true,
              outMode: "bounce",
              random: false,
              speed: 6,
              straight: false,
            },
            number: {
              density: {
                enable: true,
                value_area: 800,
              },
              value: 80,
            },
            opacity: {
              value: 0.1,
            },
            shape: {
              character: {
                fill: true,
                font: "Verdana",
                style: "",
                value: [...Array(26).keys()].map((i) =>
                  String.fromCharCode(i + 65)
                ),
                weight: "400",
              },
              image: {
                height: 100,
                replaceColor: true,
                src: "images/github.svg",
                width: 100,
              },
              polygon: { nbSides: 5 },
              stroke: { color: "#ffffff", width: 1 },
              type: "char",
            },
            size: {
              anim: { enable: true, size_min: 10, speed: 10, sync: false },
              random: false,
              value: 5,
            },
          },
          detectRetina: true,
        }}
      />
    );
  }
}

export default Backgound;
