import React from "react";
import { Figure } from "react-bootstrap";
import "./Resume.css";
import Skill from "./Skill";

export default function Resume() {
  return (
    <div id={"#resume"} className="resume-container">
      <div className="experience">
        <div className="">
          <h2>Work Experiences</h2>
          <div className="work-experience">
            <span className="work-header">
              <h4>Software Developer</h4>
              <h4>TCM Technologies LTD</h4>
            </span>
            <span>September 2020 - Present</span>
            <ul>
              <li>
                Maintain and Improve Bell Telecommunications’ internal and
                external SCADA web applications
              </li>
              <li>
                Utilize jQuery, Handlebars, and Angular for Frontend
                development, NodeJS, MongoDB for Backend development
              </li>
              <li>
                Create Cron Jobs to automate system maintenance and to write
                MongoDB scripts to update and modify large data
              </li>
              <li>
                Redevelop and redesign TCM in-house web app using React and the
                existing Perl legacy backend
              </li>
            </ul>
          </div>
          <div className="work-experience">
            <span className="work-header">
              <h4>Linux Automation QA Engineer, Co-op</h4>
              <h4>T2 Systems, Inc.</h4>
            </span>
            <span>October 2017 - August 2018</span>
            <ul>
              <li>
                Designed and executed both manual and automation test cases for
                verification and regression testing, worked closely with
                developers to track, report, and fix bugs{" "}
              </li>
              <li>
                Implemented and improved automation tests for SW application on
                Linux platform using Python Unittest framework{" "}
              </li>
              <li>
                Participated in sprint planning and retrospective, and worked in
                Agile environment with Agile development tools, such as
                Bitbucket, Jira, and Confluence
              </li>
              <li>
                Created a mock web server using Python Flask framework to handle
                HTTP requests and to test applications with specific responses{" "}
              </li>
            </ul>
          </div>
        </div>
        <div className="">
          <h2>Education</h2>
          <span className="work-header">
            <h4>Mechatronic Systems Engineering</h4>
            <h4>Simon Fraser University</h4>
          </span>
          <span>January 2015 – August 2019</span>
        </div>
      </div>
      <div className="skill">
        <h2>Skills</h2>
        <Skill />
      </div>
    </div>
  );
}
