import { useEffect } from "react";
import { withRouter } from "react-router-dom";

const ScrollHandler = ({ location }) => {
  const listenScrollEvent = (event) => {
    if (window.scrollY < 73) {
      document.querySelector(".header-bar").style = "background: none";
    } else if (window.scrollY > 70) {
      document.querySelector(".header-bar").style = "background: #222222";
    }
    if (window.scrollY < 500) {
      document
        .querySelectorAll(".header-item")
        .forEach((d) => (d.style = "color: white"));
      document.querySelectorAll(".header-item")[0].style = "color: orange";
    } else if (window.scrollY > 500 && window.scrollY < 1300) {
      document
        .querySelectorAll(".header-item")
        .forEach((d) => (d.style = "color: white"));
      document.querySelectorAll(".header-item")[1].style = "color: orange";
    } else if (window.scrollY > 1300 && window.scrollY < 2300) {
      document
        .querySelectorAll(".header-item")
        .forEach((d) => (d.style = "color: white"));
      document.querySelectorAll(".header-item")[2].style = "color: orange";
    } else if (window.scrollY > 2300 && window.scrollY < 5500) {
      document
        .querySelectorAll(".header-item")
        .forEach((d) => (d.style = "color: white"));
      document.querySelectorAll(".header-item")[3].style = "color: orange";
    } else if (window.scrollY > 5500) {
      document
        .querySelectorAll(".header-item")
        .forEach((d) => (d.style = "color: white"));
      document.querySelectorAll(".header-item")[12].style = "color: orange";
    }
  };
  useEffect(() => {
    window.addEventListener("scroll", listenScrollEvent);
    return () => window.removeEventListener("scroll", listenScrollEvent);
  }, []);

  useEffect(() => {
    const element = document.getElementById(location.hash);
    if (location.hash === "#home" || location.hash === "")
      document.querySelector(".header-bar").style = "background: none";
    else document.querySelector(".header-bar").style = "background: #222222";

    setTimeout(() => {
      window.scrollTo({
        behavior: element ? "smooth" : "auto",
        top: element ? element.offsetTop : 0,
      });
    }, 100);
  }, [location]);

  return null;
};

export default withRouter(ScrollHandler);
