import React, { useState } from "react";

import project1_1 from "../../assets/img/project1v2_1.png";
import project1_2 from "../../assets/img/project1v2_2.png";
import project1_3 from "../../assets/img/project1v2_3.png";
import project1_4 from "../../assets/img/project1v2_4.png";
import project1_5 from "../../assets/img/project1v2_5.png";
import project1_6 from "../../assets/img/project1v2_6.png";
import project1_7 from "../../assets/img/project1v2_7.png";
import project1_8 from "../../assets/img/project1v2_8.png";
import project2_1 from "../../assets/img/project2_1.png";
import project2_2 from "../../assets/img/project2_2.png";
import project3_1 from "../../assets/img/project3_1.png";
import project3_2 from "../../assets/img/project3_2.png";
import project3_3 from "../../assets/img/project3_3.png";
import project4_1 from "../../assets/img/project4_1.png";
import project4_2 from "../../assets/img/project4_2.png";
import project4_3 from "../../assets/img/project4_3.png";
import project4_4 from "../../assets/img/project4_4.png";
import project5_1 from "../../assets/img/project5_1.png";
import project5_2 from "../../assets/img/project5_2.png";
import project5_3 from "../../assets/img/project5_3.png";
import project5_4 from "../../assets/img/project5_4.png";
import project6 from "../../assets/img/project6.jpg";
import project7_1 from "../../assets/img/project7_1.jpg";
import project7_2 from "../../assets/img/project7_2.png";
import project7_3 from "../../assets/img/project7_3.png";
import project8_1 from "../../assets/img/project8_1.png";
import project8_2 from "../../assets/img/project8_2.png";
import project8_3 from "../../assets/img/project8_3.png";
import project8_4 from "../../assets/img/project8_4.png";
import project8_6 from "../../assets/img/project8_6.png";
import project8_5 from "../../assets/img/project8_5.jpg";
import project8_7 from "../../assets/img/project8_7.jpg";

import { Carousel, Button, Badge, Modal } from "react-bootstrap";
import "./Projects.css";

export default function Projects() {
  const [show0, setShow0] = useState(false);
  const [show1, setShow1] = useState(false);
  const [show2, setShow2] = useState(false);
  const [show3, setShow3] = useState(false);
  const [show4, setShow4] = useState(false);
  const [show5, setShow5] = useState(false);
  const [show6, setShow6] = useState(false);
  const [show7, setShow7] = useState(false);

  const PicCovid = [project4_1, project4_2, project4_3, project4_4];
  const PicEcom = [
    project1_1,
    project1_2,
    project1_3,
    project1_4,
    project1_5,
    project1_6,
    project1_7,
    project1_8,
  ];
  const PicChat = [project2_1, project2_2];
  const PicMenu = [project3_1, project3_2, project3_3];
  const PicIot = [project5_1, project5_2, project5_3, project5_4];
  const PicRob = [project7_1, project7_2, project7_3];
  const PicXfox = [
    project8_1,
    project8_2,
    project8_3,
    project8_4,
    project8_7,
    project8_6,
    project8_5,
  ];

  return (
    <div id={"#projects"} className="projects-container">
      <div id={"#projects-0"} className="project">
        <h2>Covid 19 Tracker</h2>
        <div className="project-content">
          <Carousel
            style={{ width: "450px", height: "330px", overflow: "hidden" }}
          >
            {PicCovid.map((pic, i) => (
              <Carousel.Item key={`covid-${i}`} onClick={() => setShow0(true)}>
                <img className="d-block w-100" src={pic} />
              </Carousel.Item>
            ))}
          </Carousel>
          <div className="text-area-right">
            This application helps track the numbers of covid-19 cases across
            Canada and around the World. It displays the data on the map with
            different locations, as well on the graph. For global data, a filter
            with auto-complete selection is used to filter the data by country,
            and the data can also be read on a table. This app updates itself
            with the latest data that gets retrieved from the Canada Health
            Website and WHO, and saved to online database daily on a set time
            using CronJob, to make sure the data is up to date.
            <div className="project-footer">
              <div className="stack-tag">
                <Badge variant="secondary">
                  <i class="fab fa-node-js"></i> Nodejs
                </Badge>
                <Badge variant="secondary">Express</Badge>
                <Badge variant="secondary">
                  <i class="fab fa-react"></i> React
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-database"></i> MongoDB
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-cloud"></i> MongoDB Atlas
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-undo-alt"></i> CronJob
                </Badge>
              </div>
              <div className="link-btn">
                <Button
                  variant="light"
                  style={{ color: "black" }}
                  href="http://www.github.com/jackneng/Covid19-Tracker"
                  target="_blank"
                >
                  <i class="fab fa-github"></i> Github
                </Button>
                <Button
                  href="https://distracted-banach-1c98f6.netlify.app"
                  target="_blank"
                >
                  Visit Here <i class="fas fa-caret-right"></i>
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div id={"#projects-1"} className="project">
        <h2>E-Commerce Website</h2>
        <div className="project-content">
          <div className="text-area-right">
            This is a E-Commerce website that let people to do shopping online.
            It was nicely designed to have a simple online-purchase user-flow
            for customers to shop. Users can also create an account so as to
            have a easy customized experience, such as setting up the shipping
            info and checking the order history.
            <div className="project-footer">
              <div className="stack-tag">
                <Badge variant="secondary">
                  <i class="fab fa-react"></i> React
                </Badge>
                <Badge variant="secondary">
                  <i class="fab fa-java"></i> Spring Boot
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-key"></i> JWT
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-database"></i> MySQL
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-cloud"></i> AWS Rds
                </Badge>
              </div>
              <div className="link-btn">
                <Button
                  variant="light"
                  style={{ color: "black" }}
                  href="http://www.github.com/jackneng/E-Commerce"
                  target="_blank"
                >
                  <i class="fab fa-github"></i> Github
                </Button>
                <Button
                  href="https://competent-minsky-ccccf8.netlify.app/"
                  target="_blank"
                >
                  Visit Here <i class="fas fa-caret-right"></i>
                </Button>
              </div>
            </div>
          </div>
          <Carousel
            style={{ width: "450px", height: "220px", overflow: "hidden" }}
          >
            {PicEcom.map((pic, i) => (
              <Carousel.Item key={`ecom-${i}`} onClick={() => setShow1(true)}>
                <img className="d-block w-100" src={pic} />
              </Carousel.Item>
            ))}
          </Carousel>
        </div>
      </div>
      <div id={"#projects-2"} className="project">
        <h2>Chat Application</h2>
        <div className="project-content">
          <Carousel
            style={{ width: "450px", height: "230px", overflow: "hidden" }}
          >
            {PicChat.map((pic, i) => (
              <Carousel.Item key={`chat-${i}`} onClick={() => setShow2(true)}>
                <img className="d-block w-100" src={pic} />
              </Carousel.Item>
            ))}
          </Carousel>
          <div className="text-area-right">
            This project is a chat application that allows people to join into a
            chat room and chat. The UI was designed using JavaScript React
            frameworks, which adopted most of the modern chat app features, such
            as a welcoming message from bot, and people's favourite emojis. The
            server developed with Nodejs and Express was created to support the
            application, where Socket.io was used to help doing real-time
            messaging.
            <div className="project-footer">
              <div className="stack-tag">
                <Badge variant="secondary">
                  <i class="fab fa-node-js"></i> Nodejs
                </Badge>
                <Badge variant="secondary">
                  <i class="fab fa-node-js"></i> Express
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-bolt"></i> Socketio
                </Badge>
                <Badge variant="secondary">
                  <i class="fab fa-react"></i> React
                </Badge>
              </div>
              <div className="link-btn">
                <Button
                  variant="light"
                  style={{ color: "black" }}
                  href="http://www.github.com/jackneng/Chat-Room"
                  target="_blank"
                >
                  <i class="fab fa-github"></i> Github
                </Button>
                <Button
                  href="https://goofy-swirles-a01bfd.netlify.app"
                  target="_blank"
                >
                  Visit Here <i class="fas fa-caret-right"></i>
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div id={"#projects-3"} className="project">
        <h2>Menu Hosting Service</h2>
        <div className="project-content">
          <div className="text-area-right">
            Menu Hosting Service allows restaurant to create an account and post
            their Happy Hour/Special Menus online. A backend server was
            developed using Python Django and Django Rest frameworks. Also a
            user authentication service for restaurant account was implemented
            with the use of Python knox framework. A user-friendly website for
            people to check out the restaurants' menus and their contact info
            was designed by using JavaScript React, Redux and Axios frameworks
            and Bootstrap.
            <br></br>
            <div className="project-footer">
              <div className="stack-tag">
                <Badge variant="secondary">
                  <i class="fab fa-react"></i> React
                </Badge>
                <Badge variant="secondary">
                  <i class="fab fa-python"></i> Django
                </Badge>
                <Badge variant="secondary">
                  <i class="fab fa-python"></i> Knox
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-database"></i> sqlite
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-cloud"></i> AWS EC2
                </Badge>
              </div>
              <div className="link-btn">
                <Button
                  variant="light"
                  style={{ color: "black" }}
                  href="http://www.github.com/jackneng/Happy-Hour-Menu"
                  target="_blank"
                >
                  <i class="fab fa-github"></i> Github
                </Button>
                <Button href="http://18.220.149.130/#/menus" target="_blank">
                  Visit Here <i class="fas fa-caret-right"></i>
                </Button>
              </div>
            </div>
          </div>
          <Carousel
            style={{ width: "450px", height: "220px", overflow: "hidden" }}
          >
            {PicMenu.map((pic, i) => (
              <Carousel.Item key={`menu-${i}`} onClick={() => setShow3(true)}>
                <img className="d-block w-100" src={pic} />
              </Carousel.Item>
            ))}
          </Carousel>
        </div>
      </div>
      <div id={"#projects-4"} className="project">
        <h2>iPark IOT Parking System</h2>
        <div className="project-content">
          <Carousel
            style={{ width: "450px", height: "220px", overflow: "hidden" }}
          >
            {PicIot.map((pic, i) => (
              <Carousel.Item key={`iot-${i}`} onClick={() => setShow4(true)}>
                <img className="d-block w-100" src={pic} />
              </Carousel.Item>
            ))}
          </Carousel>
          <div className="text-area-right">
            This project integrated embedded microcontroller to the Google
            Firestore database and provided a web application with user-friendly
            GUI designed for drivers. The intention is for the drivers to easily
            have a real-time map of the designated parking area, whether it is
            for available spots’ locations or to book a spot beforehand or for
            the parking lot administrator to control the vehicle’s flow more
            efficiently.
            <div className="project-footer">
              <div className="stack-tag">
                <Badge variant="secondary">
                  <i class="fas fa-code"></i> Arduino
                </Badge>
                <Badge variant="secondary">
                  <i class="fab fa-bluetooth-b"></i> IoT
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-wifi"></i> ESP8266
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-cloud"></i> Firebase
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-bezier-curve"></i> Circuit Design
                </Badge>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div id={"#projects-5"} className="project">
        <h2>Robot Arm</h2>
        <div className="project-content">
          <div className="text-area-right">
            This project consists of 3 different fields: mechanical, electrical
            and control. For the mechanical part, a robot arm truss was designed
            and built. For the electrical part, electrical components such as
            MOSFET, H-bridge were used to drive the motor and electromagnet.
            Last, Simulink and MATLAB were used to control the movements of the
            robot arm in the control part.
            <div className="project-footer">
              <div className="stack-tag">
                <Badge variant="secondary">
                  <i class="fas fa-code"></i> MATLAB
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-code"></i>Arduino
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-cog"></i> PID
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-bezier-curve"></i> Circuit Design
                </Badge>
              </div>
            </div>
          </div>
          <Carousel
            style={{ width: "450px", height: "220px", overflow: "hidden" }}
          >
            {PicRob.map((pic, i) => (
              <Carousel.Item key={`robot-${i}`} onClick={() => setShow5(true)}>
                <img className="d-block w-100" src={pic} />
              </Carousel.Item>
            ))}
          </Carousel>
        </div>
      </div>
      <div id={"#projects-6"} className="project">
        <h2>Self-Balancing Robot</h2>
        <div className="project-content">
          <div
            className="picture-container"
            style={{ width: "450px" }}
            onClick={() => setShow6(true)}
          >
            <img style={{ width: "100%" }} src={project6} alt="" />
          </div>
          <div className="text-area-right">
            Self-Balancing Robot has a double-wheeled inverted pendulum
            mechanism, and it can maintain its upright position by generating
            counter-balance torque at the wheels according to data obtain
            through gyroscope and accelerometer. This project emphasized on
            developing a closed-loop control system using Tiva™ microcontroller,
            which involved using I2C communication with IMU and PWM for
            controlling output to motors.
            <div className="project-footer">
              <div className="stack-tag">
                <Badge variant="secondary">
                  <i class="fas fa-cog"></i> H-Bridge
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-cog"></i> PID
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-microchip"></i> Tiva C MCU
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-microchip"></i> Embedded System
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-bezier-curve"></i> Circuit Design
                </Badge>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div id={"#projects-7"} className="project">
        <h2>XFOX Image Plotter</h2>
        <div className="project-content">
          <div className="text-area-right">
            As a team participated in Rembrandt Robotics Competition, we
            designed and built a robot to reproduce a hard copied image. The
            robot first takes an image input to the MATLAB GUI, then it starts
            processing image, which will get the options of the several images
            with different edges by different image processing algorithms, as
            well as the colours detected. After that users can choose the
            processed image and colours, and the image edge data, i.e. pixel,
            will be output to a txt file, with colour data output to map with
            rgb array. The data then gets input to the arduino code and the
            robot will start controlling the stepper motors and servo motors to
            do the printing. With all the efforts we made in the competition, we
            ended up winning the first place.
            <div className="project-footer">
              <div className="stack-tag">
                <Badge variant="secondary">
                  <i class="fas fa-code"></i> Arduino
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-code"></i>Image Processing
                </Badge>
                <Badge variant="secondary">
                  <i class="fas fa-code"></i> MATLAB
                </Badge>
              </div>
            </div>
          </div>
          <Carousel
            style={{ width: "450px", height: "280px", overflow: "hidden" }}
          >
            {PicXfox.map((pic, i) => (
              <Carousel.Item key={`xfox-${i}`} onClick={() => setShow7(true)}>
                <img className="d-block w-100" src={pic} />
              </Carousel.Item>
            ))}
          </Carousel>
        </div>
      </div>

      <Modal
        show={show0}
        onHide={() => setShow0(false)}
        animation={false}
        dialogClassName="modal-carousel"
        centered
      >
        <Carousel style={{ overflow: "hidden" }}>
          {PicCovid.map((pic, i) => (
            <Carousel.Item key={`covid-${i}`}>
              <img className="d-block w-100" src={pic} />
            </Carousel.Item>
          ))}
        </Carousel>
      </Modal>
      <Modal
        show={show1}
        onHide={() => setShow1(false)}
        animation={false}
        dialogClassName="modal-carousel"
        centered
      >
        <Carousel style={{ overflow: "hidden" }}>
          {PicEcom.map((pic, i) => (
            <Carousel.Item key={`ecom-${i}`}>
              <img className="d-block w-100" src={pic} />
            </Carousel.Item>
          ))}
        </Carousel>
      </Modal>
      <Modal
        show={show2}
        onHide={() => setShow2(false)}
        animation={false}
        dialogClassName="modal-carousel"
        centered
      >
        <Carousel style={{ overflow: "hidden" }}>
          {PicChat.map((pic, i) => (
            <Carousel.Item key={`chat-${i}`}>
              <img className="d-block w-100" src={pic} />
            </Carousel.Item>
          ))}
        </Carousel>
      </Modal>
      <Modal
        show={show3}
        onHide={() => setShow3(false)}
        animation={false}
        dialogClassName="modal-carousel"
        centered
      >
        <Carousel style={{ overflow: "hidden" }}>
          {PicMenu.map((pic, i) => (
            <Carousel.Item key={`menu-${i}`}>
              <img className="d-block w-100" src={pic} />
            </Carousel.Item>
          ))}
        </Carousel>
      </Modal>
      <Modal
        show={show4}
        onHide={() => setShow4(false)}
        animation={false}
        dialogClassName="modal-carousel"
        centered
      >
        <Carousel style={{ overflow: "hidden" }}>
          {PicIot.map((pic, i) => (
            <Carousel.Item key={`iot-${i}`}>
              <img className="d-block w-100" src={pic} />
            </Carousel.Item>
          ))}
        </Carousel>
      </Modal>
      <Modal
        show={show5}
        onHide={() => setShow5(false)}
        animation={false}
        dialogClassName="modal-carousel"
        centered
      >
        <Carousel style={{ overflow: "hidden" }}>
          {PicRob.map((pic, i) => (
            <Carousel.Item key={`robot-${i}`}>
              <img className="d-block w-100" src={pic} />
            </Carousel.Item>
          ))}
        </Carousel>
      </Modal>
      <Modal
        show={show6}
        onHide={() => setShow6(false)}
        animation={false}
        dialogClassName="modal-carousel"
        centered
      >
        <img style={{ width: "100%" }} src={project6} alt="" />
      </Modal>
      <Modal
        show={show7}
        onHide={() => setShow7(false)}
        animation={false}
        dialogClassName="modal-carousel"
        centered
      >
        <Carousel style={{ overflow: "hidden" }}>
          {PicXfox.map((pic, i) => (
            <Carousel.Item key={`xfox-${i}`}>
              <img className="d-block w-100" src={pic} />
            </Carousel.Item>
          ))}
        </Carousel>
      </Modal>
    </div>
  );
}
